.button {
  text-align: center;
  padding: 10px;
  border: 12px solid #370B18;
  width: 100px;
  height: 50px;
  background-color: #983351;
  border-radius: 10px;
}

body {
  background-color: black;height: 100%;
}

.container {
  display: flex; /* or inline-flex */
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
}

.outerContainer {
  
  display: flex; /* or inline-flex */
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
}

#card {
  color: #370B18;
  display: none;
  width: 400px;
  height: 600px;
  border-radius: 25px;
  border: 10px solid #370B18;
  background-color: #983351;
  text-align: center;
}

#scrollingContent {
  font-weight:900;
  font: 36px Consolas;
}

#intensity {
  color: #370B18;
  font-size: 30px;
  font-weight: 900;
  font-family: sans-serif;
}

#finale {
  font-size: 30px;
  font-weight: 900;
  
  border: 2px solid gold;
  height: 100%;
}

.img {
  width: auto;
  height: 250px;
  border: 2px solid silver;

}

.img_left {
  float: left;
}

.img_right {
  float: right;
}

.clear {
  clear: both;
}